const { declareModuleExports } = require("@babel/types");

let demo = document.querySelector("#html");
let style = document.querySelector('#style');
style.innerHTML = `
body{
    color: blue
}
`
let n = 0;
let string = `
/* 你好，我是一名前端新人
接下來我要加样式了
我要加的样式是 */
body{
    color: blue
}
`
string = string.replace(/\n/g, "<br>");
demo.innerHTML = string.substring(0, n);


let step = () => {
    setTimeout(() => {
        n = n + 1;
        demo.innerHTML = string.substring(0, n);
        if (n < string.length) {
            step();
        }
    }, 100);
};

step();

